	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2012/8/twitter-blocks-tumblrs-find-friends-access-too/index.md absPath of file >>> MarkdownRemark","html":"<p>Marco Arment on Twitter’s blocking of Tumblr “find friends” access</p>\n<blockquote>\n<p>Twitter will now only permit large services to add value to Twitter, not get any value from it.   </p>\n<p>So when Twitter says something like “[developers] will need to work with us” to “identify areas of value” when they get big enough for Twitter to notice, I don’t have high hopes for what “working with us” might actually entail. </p>\n</blockquote>\n<p>Twitter clearly see themselves in a strong enough position to be safe from being disrupted. It’s certainly hard to argue against it.</p>","frontmatter":{"title":"Twitter blocks Tumblr's \"find friends\" access too","date":"August 23, 2012","passthroughUrl":"http://www.marco.org/"}}},"pathContext":{"slug":"/blog/2012/8/twitter-blocks-tumblrs-find-friends-access-too/","previous":{"fields":{"slug":"/blog/2012/8/protocols-dont-mean-much/"},"frontmatter":{"title":"Protocols don't mean much","passthroughUrl":"http://scripting.com/stories/2012/08/22/protocolsDontMeanMuch.html","type":null}},"next":{"fields":{"slug":"/blog/V2h5LXdhaXRpbmct/"},"frontmatter":{"title":"Why waiting in line is torture","passthroughUrl":"http://www.nytimes.com/2012/08/19/opinion/sunday/why-waiting-in-line-is-torture.html?_r=1&pagewanted=all","type":null}}}}

